'use client'

import { useEffect, useRef } from 'react'

export default function PhaserGame() {
  const gameRef = useRef<HTMLDivElement>(null)
  const phaserGameRef = useRef<any>(null)

  useEffect(() => {
    if (!gameRef.current || phaserGameRef.current || typeof window === 'undefined') return

    // Dynamic import of Phaser to avoid SSR issues
    import('phaser').then((Phaser) => {
      // Phaser game configuration
      const config: Phaser.Types.Core.GameConfig = {
        type: Phaser.AUTO,
        width: 800,
        height: 600,
        parent: gameRef.current!,
        backgroundColor: '#2c3e50',
        scene: {
          preload: function(this: Phaser.Scene) {
            // Create placeholder graphics for roosters
            this.add.graphics()
              .fillStyle(0xff0000)
              .fillRect(0, 0, 64, 64)
              .generateTexture('player-rooster', 64, 64)

            this.add.graphics()
              .fillStyle(0x0000ff)
              .fillRect(0, 0, 64, 64)
              .generateTexture('enemy-rooster', 64, 64)

            // Create UI elements
            this.add.graphics()
              .fillStyle(0x00ff00)
              .fillRect(0, 0, 200, 20)
              .generateTexture('hp-bar-bg', 200, 20)

            this.add.graphics()
              .fillStyle(0xff0000)
              .fillRect(0, 0, 200, 20)
              .generateTexture('hp-bar-fill', 200, 20)
          },
          create: function(this: Phaser.Scene) {
            // Battle background
            this.add.rectangle(400, 300, 800, 600, 0x87CEEB)
            
            // Ground
            this.add.rectangle(400, 550, 800, 100, 0x90EE90)
            
            // Player rooster (left side)
            const playerRooster = this.add.image(200, 400, 'player-rooster')
            playerRooster.setScale(1.5)
            
            // Enemy rooster (right side)
            const enemyRooster = this.add.image(600, 400, 'enemy-rooster')
            enemyRooster.setScale(1.5)
            enemyRooster.setFlipX(true)

            // Player HP bar
            this.add.text(50, 50, 'Embawk', { fontSize: '18px', color: '#000' })
            const playerHpBg = this.add.image(150, 70, 'hp-bar-bg')
            playerHpBg.setOrigin(0, 0.5)
            const playerHpFill = this.add.image(150, 70, 'hp-bar-fill')
            playerHpFill.setOrigin(0, 0.5)
            playerHpFill.setScale(0.8, 1) // 80% HP

            // Enemy HP bar
            this.add.text(550, 50, 'Enemy Boltick', { fontSize: '18px', color: '#000' })
            const enemyHpBg = this.add.image(450, 70, 'hp-bar-bg')
            enemyHpBg.setOrigin(0, 0.5)
            const enemyHpFill = this.add.image(450, 70, 'hp-bar-fill')
            enemyHpFill.setOrigin(0, 0.5)
            enemyHpFill.setScale(0.6, 1) // 60% HP

            // Battle UI
            const uiPanel = this.add.rectangle(400, 500, 780, 180, 0xffffff)
            uiPanel.setStrokeStyle(2, 0x000000)

            // Move buttons
            const moves = ['Fire Blast', 'Peck', 'Wing Attack', 'Roost']
            moves.forEach((move, index) => {
              const x = 150 + (index % 2) * 200
              const y = 450 + Math.floor(index / 2) * 50
              
              const button = this.add.rectangle(x, y, 180, 40, 0xe74c3c)
              button.setStrokeStyle(2, 0x000000)
              button.setInteractive()
              
              this.add.text(x, y, move, { 
                fontSize: '14px', 
                color: '#fff' 
              }).setOrigin(0.5)

              button.on('pointerdown', () => {
                console.log(`Used ${move}!`)
                // Add battle logic here
              })

              button.on('pointerover', () => {
                button.setFillStyle(0xc0392b)
              })

              button.on('pointerout', () => {
                button.setFillStyle(0xe74c3c)
              })
            })

            // Battle text
            this.add.text(400, 520, 'Choose your move!', {
              fontSize: '16px',
              color: '#000'
            }).setOrigin(0.5)

            // Add some battle animations
            this.tweens.add({
              targets: playerRooster,
              y: playerRooster.y - 10,
              duration: 1000,
              yoyo: true,
              repeat: -1,
              ease: 'Sine.easeInOut'
            })

            this.tweens.add({
              targets: enemyRooster,
              y: enemyRooster.y - 8,
              duration: 1200,
              yoyo: true,
              repeat: -1,
              ease: 'Sine.easeInOut'
            })
          },
          update: function(this: Phaser.Scene) {
            // Game loop - add battle logic here
          }
        },
        physics: {
          default: 'arcade',
          arcade: {
            gravity: { x: 0, y: 0 },
            debug: false
          }
        }
      }

      // Initialize Phaser game
      phaserGameRef.current = new Phaser.Game(config)
    })

    // Cleanup function
    return () => {
      if (phaserGameRef.current) {
        phaserGameRef.current.destroy(true)
        phaserGameRef.current = null
      }
    }
  }, [])

  return (
    <div className="fixed inset-0 bg-black bg-opacity-75 flex items-center justify-center z-50">
      <div className="bg-white rounded-lg p-4">
        <div className="flex justify-between items-center mb-4">
          <h2 className="text-xl font-bold">Battle Arena</h2>
          <button
            onClick={() => {
              if (phaserGameRef.current) {
                phaserGameRef.current.destroy(true)
                phaserGameRef.current = null
              }
            }}
            className="text-gray-500 hover:text-gray-700 text-2xl"
          >
            √ó
          </button>
        </div>
        <div ref={gameRef} className="border border-gray-300 rounded" />
      </div>
    </div>
  )
}
