'use client'

import { useState } from 'react'
import { useAccount, useConnect, useDisconnect } from 'wagmi'
import AccountCreation from '@/components/AccountCreation'
import RoosterSelection from '@/components/RoosterSelection'
import BattleFinder from '@/components/BattleFinder'
import Training from '@/components/Training'
import PhaserGame from '@/components/PhaserGameNew'

type GameState = 'welcome' | 'account-creation' | 'rooster-selection' | 'main-menu' | 'battle' | 'training'

export default function Home() {
  const [gameState, setGameState] = useState<GameState>('welcome')
  const [selectedRooster, setSelectedRooster] = useState<string | null>(null)
  const { address, isConnected } = useAccount()
  const { connect, connectors } = useConnect()
  const { disconnect } = useDisconnect()

  const handleRoosterSelection = (roosterType: string) => {
    setSelectedRooster(roosterType)
    setGameState('main-menu')
  }

  const renderCurrentState = () => {
    switch (gameState) {
      case 'welcome':
        return (
          <div className="min-h-screen bg-gradient-to-br from-slate-900 to-slate-800 flex items-center justify-center p-4">
            <div className="max-w-md w-full bg-white rounded-lg shadow-xl p-8 text-center">
              <h1 className="text-4xl font-bold text-gray-800 mb-4">Rooster Battle</h1>
              <p className="text-gray-600 mb-8">
                Welcome to the ultimate rooster battling experience! Train your roosters, battle other players, and climb the ranks.
              </p>
              <button
                onClick={() => setGameState('account-creation')}
                className="w-full bg-blue-600 hover:bg-blue-700 text-white font-semibold py-3 px-6 rounded-lg transition-colors"
              >
                Start Your Journey
              </button>
            </div>
          </div>
        )

      case 'account-creation':
        return <AccountCreation onComplete={() => setGameState('rooster-selection')} />

      case 'rooster-selection':
        return <RoosterSelection onSelection={handleRoosterSelection} />

      case 'main-menu':
        return (
          <div className="min-h-screen bg-gradient-to-br from-slate-900 to-slate-800 p-4">
            <div className="max-w-4xl mx-auto">
              <header className="bg-white rounded-lg shadow-lg p-6 mb-6">
                <div className="flex justify-between items-center">
                  <div>
                    <h1 className="text-3xl font-bold text-gray-800">Rooster Battle Arena</h1>
                    <p className="text-gray-600">Your rooster: {selectedRooster}</p>
                  </div>
                  <div className="flex gap-4">
                    {!isConnected ? (
                      <button
                        onClick={() => connect({ connector: connectors[0] })}
                        className="bg-blue-600 hover:bg-blue-700 text-white px-4 py-2 rounded-lg"
                      >
                        Connect Wallet
                      </button>
                    ) : (
                      <div className="flex items-center gap-2">
                        <span className="text-sm text-gray-600">
                          {address?.slice(0, 6)}...{address?.slice(-4)}
                        </span>
                        <button
                          onClick={() => disconnect()}
                          className="bg-red-600 hover:bg-red-700 text-white px-4 py-2 rounded-lg text-sm"
                        >
                          Disconnect
                        </button>
                      </div>
                    )}
                  </div>
                </div>
              </header>

              <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
                <div className="bg-white rounded-lg shadow-lg p-6">
                  <h2 className="text-xl font-semibold text-gray-800 mb-4">Battle Arena</h2>
                  <p className="text-gray-600 mb-4">Challenge other players to epic battles</p>
                  <button
                    onClick={() => setGameState('battle')}
                    className="w-full bg-red-600 hover:bg-red-700 text-white font-semibold py-2 px-4 rounded-lg"
                  >
                    Find Battle
                  </button>
                </div>

                <div className="bg-white rounded-lg shadow-lg p-6">
                  <h2 className="text-xl font-semibold text-gray-800 mb-4">Training Ground</h2>
                  <p className="text-gray-600 mb-4">Train your rooster to improve stats</p>
                  <button
                    onClick={() => setGameState('training')}
                    className="w-full bg-green-600 hover:bg-green-700 text-white font-semibold py-2 px-4 rounded-lg"
                  >
                    Train Rooster
                  </button>
                </div>

                <div className="bg-white rounded-lg shadow-lg p-6">
                  <h2 className="text-xl font-semibold text-gray-800 mb-4">Wilderness</h2>
                  <p className="text-gray-600 mb-4">Explore and catch wild roosters</p>
                  <button
                    className="w-full bg-purple-600 hover:bg-purple-700 text-white font-semibold py-2 px-4 rounded-lg"
                    disabled
                  >
                    Coming Soon
                  </button>
                </div>
              </div>
            </div>
          </div>
        )

      case 'battle':
        return <BattleFinder onBack={() => setGameState('main-menu')} />

      case 'training':
        return <Training onBack={() => setGameState('main-menu')} />

      default:
        return null
    }
  }

  return (
    <div className="min-h-screen">
      {renderCurrentState()}
      {gameState === 'battle' && <PhaserGame />}
    </div>
  )
}
