'use client'

import { useState } from 'react'
import { useForm } from 'react-hook-form'

interface WagerData {
  token: string
  amount: string
}

interface WagerMatchModalProps {
  onClose: () => void
  onConfirm: (wagerData: WagerData) => void
}

const supportedTokens = [
  { symbol: 'USDC', name: 'USD Coin', address: '0x833589fCD6eDb6E08f4c7C32D4f71b54bdA02913' },
  { symbol: 'ETH', name: 'Ethereum', address: '0x0000000000000000000000000000000000000000' },
  { symbol: 'WETH', name: 'Wrapped Ethereum', address: '0x4200000000000000000000000000000000000006' },
]

export default function WagerMatchModal({ onClose, onConfirm }: WagerMatchModalProps) {
  const [isLoading, setIsLoading] = useState(false)
  const { register, handleSubmit, watch, formState: { errors } } = useForm<WagerData>()

  const selectedToken = watch('token')
  const amount = watch('amount')

  const onSubmit = async (data: WagerData) => {
    setIsLoading(true)
    
    try {
      // Simulate wallet interaction
      await new Promise(resolve => setTimeout(resolve, 2000))
      onConfirm(data)
    } catch (error) {
      console.error('Wager creation failed:', error)
      alert('Failed to create wager match. Please try again.')
    } finally {
      setIsLoading(false)
    }
  }

  return (
    <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center p-4 z-50">
      <div className="bg-white rounded-lg shadow-xl max-w-md w-full p-6">
        <div className="flex justify-between items-center mb-6">
          <h2 className="text-2xl font-bold text-gray-800">Create Wager Match</h2>
          <button
            onClick={onClose}
            className="text-gray-500 hover:text-gray-700 text-2xl"
          >
            √ó
          </button>
        </div>

        <form onSubmit={handleSubmit(onSubmit)} className="space-y-6">
          <div>
            <label htmlFor="token" className="block text-sm font-medium text-gray-700 mb-2">
              Select Token
            </label>
            <select
              {...register('token', { required: 'Please select a token' })}
              id="token"
              className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-500 focus:border-transparent"
            >
              <option value="">Choose a token...</option>
              {supportedTokens.map((token) => (
                <option key={token.symbol} value={token.symbol}>
                  {token.symbol} - {token.name}
                </option>
              ))}
            </select>
            {errors.token && (
              <p className="mt-1 text-sm text-red-600">{errors.token.message}</p>
            )}
          </div>

          <div>
            <label htmlFor="amount" className="block text-sm font-medium text-gray-700 mb-2">
              Wager Amount
            </label>
            <input
              {...register('amount', { 
                required: 'Please enter an amount',
                pattern: {
                  value: /^\d+(\.\d+)?$/,
                  message: 'Please enter a valid number'
                },
                min: {
                  value: 0.001,
                  message: 'Minimum wager is 0.001'
                }
              })}
              type="number"
              step="0.001"
              id="amount"
              className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-500 focus:border-transparent"
              placeholder="0.00"
            />
            {errors.amount && (
              <p className="mt-1 text-sm text-red-600">{errors.amount.message}</p>
            )}
          </div>

          {selectedToken && amount && (
            <div className="bg-blue-50 p-4 rounded-lg">
              <h3 className="font-semibold text-blue-800 mb-2">Wager Summary</h3>
              <div className="space-y-1 text-sm text-blue-700">
                <p>Token: {selectedToken}</p>
                <p>Amount: {amount} {selectedToken}</p>
                <p className="text-xs text-blue-600 mt-2">
                  * Winner receives 98% of the total prize pool (both wagers)
                </p>
                <p className="text-xs text-blue-600">
                  * 2% platform fee applies
                </p>
              </div>
            </div>
          )}

          <div className="bg-yellow-50 p-4 rounded-lg">
            <div className="flex items-start">
              <span className="text-yellow-600 mr-2">‚ö†Ô∏è</span>
              <div className="text-sm text-yellow-800">
                <p className="font-semibold mb-1">Important:</p>
                <ul className="space-y-1 text-xs">
                  <li>‚Ä¢ Your tokens will be locked until the match is complete</li>
                  <li>‚Ä¢ Both players must approve the wager before battle starts</li>
                  <li>‚Ä¢ Winner takes 98% of the total prize pool</li>
                  <li>‚Ä¢ Make sure you have sufficient balance in your wallet</li>
                </ul>
              </div>
            </div>
          </div>

          <div className="flex gap-3">
            <button
              type="button"
              onClick={onClose}
              className="flex-1 bg-gray-500 hover:bg-gray-600 text-white font-semibold py-2 px-4 rounded-lg"
            >
              Cancel
            </button>
            <button
              type="submit"
              disabled={isLoading}
              className="flex-1 bg-blue-600 hover:bg-blue-700 disabled:bg-blue-400 text-white font-semibold py-2 px-4 rounded-lg"
            >
              {isLoading ? 'Creating...' : 'Create Wager'}
            </button>
          </div>
        </form>
      </div>
    </div>
  )
}
