'use client'

import { useState } from 'react'
import { useForm } from 'react-hook-form'

interface AccountCreationProps {
  onComplete: () => void
}

interface FormData {
  username: string
  email: string
}

export default function AccountCreation({ onComplete }: AccountCreationProps) {
  const [isLoading, setIsLoading] = useState(false)
  const [showBonus, setShowBonus] = useState(false)
  
  const { register, handleSubmit, formState: { errors } } = useForm<FormData>()

  const onSubmit = async (data: FormData) => {
    setIsLoading(true)
    
    try {
      // Simulate API call to create account
      await new Promise(resolve => setTimeout(resolve, 1500))
      
      // Show signup bonus
      setShowBonus(true)
      
      // Auto-proceed after showing bonus
      setTimeout(() => {
        onComplete()
      }, 3000)
    } catch (error) {
      console.error('Account creation failed:', error)
    } finally {
      setIsLoading(false)
    }
  }

  if (showBonus) {
    return (
      <div className="min-h-screen bg-gradient-to-br from-slate-900 to-slate-800 flex items-center justify-center p-4">
        <div className="max-w-md w-full bg-white rounded-lg shadow-xl p-8 text-center">
          <div className="mb-6">
            <div className="w-16 h-16 bg-green-100 rounded-full flex items-center justify-center mx-auto mb-4">
              <span className="text-2xl">üéâ</span>
            </div>
            <h2 className="text-2xl font-bold text-gray-800 mb-2">Welcome Bonus!</h2>
            <p className="text-gray-600 mb-6">
              Congratulations! You've received your starter bonus:
            </p>
          </div>
          
          <div className="space-y-3 mb-6">
            <div className="bg-blue-50 p-3 rounded-lg">
              <span className="font-semibold text-blue-800">100 Energy Points</span>
            </div>
            <div className="bg-yellow-50 p-3 rounded-lg">
              <span className="font-semibold text-yellow-800">10 Bock Balls</span>
            </div>
          </div>
          
          <p className="text-sm text-gray-500">
            Proceeding to rooster selection...
          </p>
        </div>
      </div>
    )
  }

  return (
    <div className="min-h-screen bg-gradient-to-br from-slate-900 to-slate-800 flex items-center justify-center p-4">
      <div className="max-w-md w-full bg-white rounded-lg shadow-xl p-8">
        <div className="text-center mb-8">
          <h2 className="text-3xl font-bold text-gray-800 mb-2">Create Your Account</h2>
          <p className="text-gray-600">
            Join the Rooster Battle arena and start your journey!
          </p>
        </div>

        <form onSubmit={handleSubmit(onSubmit)} className="space-y-6">
          <div>
            <label htmlFor="username" className="block text-sm font-medium text-gray-700 mb-2">
              Username
            </label>
            <input
              {...register('username', { 
                required: 'Username is required',
                minLength: { value: 3, message: 'Username must be at least 3 characters' }
              })}
              type="text"
              id="username"
              className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-500 focus:border-transparent"
              placeholder="Enter your username"
            />
            {errors.username && (
              <p className="mt-1 text-sm text-red-600">{errors.username.message}</p>
            )}
          </div>

          <div>
            <label htmlFor="email" className="block text-sm font-medium text-gray-700 mb-2">
              Email
            </label>
            <input
              {...register('email', { 
                required: 'Email is required',
                pattern: {
                  value: /^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,}$/i,
                  message: 'Invalid email address'
                }
              })}
              type="email"
              id="email"
              className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-500 focus:border-transparent"
              placeholder="Enter your email"
            />
            {errors.email && (
              <p className="mt-1 text-sm text-red-600">{errors.email.message}</p>
            )}
          </div>

          <button
            type="submit"
            disabled={isLoading}
            className="w-full bg-blue-600 hover:bg-blue-700 disabled:bg-blue-400 text-white font-semibold py-3 px-6 rounded-lg transition-colors"
          >
            {isLoading ? 'Creating Account...' : 'Create Account'}
          </button>
        </form>

        <div className="mt-6 text-center">
          <p className="text-xs text-gray-500">
            By creating an account, you agree to our terms of service and privacy policy.
          </p>
        </div>
      </div>
    </div>
  )
}
