'use client'

import { useState } from 'react'

interface RoosterSelectionProps {
  onSelection: (roosterType: string) => void
}

interface Rooster {
  id: string
  name: string
  type: string
  description: string
  color: string
  stats: {
    strength: number
    speed: number
    stamina: number
    defense: number
    intelligence: number
  }
}

const starterRoosters: Rooster[] = [
  {
    id: 'embawk',
    name: 'Embawk',
    type: 'Fire',
    description: 'A fierce fire-type rooster with blazing attacks and high strength.',
    color: 'bg-red-500',
    stats: { strength: 85, speed: 70, stamina: 75, defense: 65, intelligence: 70 }
  },
  {
    id: 'boltick',
    name: 'Boltick',
    type: 'Electric',
    description: 'Lightning-fast electric rooster with shocking speed and intelligence.',
    color: 'bg-yellow-500',
    stats: { strength: 70, speed: 90, stamina: 65, defense: 60, intelligence: 85 }
  },
  {
    id: 'dripple',
    name: 'Dripple',
    type: 'Water',
    description: 'Calm water-type rooster with excellent defense and stamina.',
    color: 'bg-blue-500',
    stats: { strength: 65, speed: 60, stamina: 90, defense: 85, intelligence: 75 }
  },
  {
    id: 'sproutchick',
    name: 'Sproutchick',
    type: 'Nature',
    description: 'Nature-loving rooster with balanced stats and healing abilities.',
    color: 'bg-green-500',
    stats: { strength: 75, speed: 75, stamina: 80, defense: 75, intelligence: 80 }
  }
]

export default function RoosterSelection({ onSelection }: RoosterSelectionProps) {
  const [selectedRooster, setSelectedRooster] = useState<string | null>(null)
  const [showConfirmation, setShowConfirmation] = useState(false)

  const handleSelection = (roosterId: string) => {
    setSelectedRooster(roosterId)
    setShowConfirmation(true)
  }

  const confirmSelection = () => {
    if (selectedRooster) {
      const rooster = starterRoosters.find(r => r.id === selectedRooster)
      onSelection(rooster?.name || selectedRooster)
    }
  }

  const selectedRoosterData = starterRoosters.find(r => r.id === selectedRooster)

  if (showConfirmation && selectedRoosterData) {
    return (
      <div className="min-h-screen bg-gradient-to-br from-slate-900 to-slate-800 flex items-center justify-center p-4">
        <div className="max-w-md w-full bg-white rounded-lg shadow-xl p-8">
          <div className="text-center mb-6">
            <h2 className="text-2xl font-bold text-gray-800 mb-2">Confirm Your Choice</h2>
            <p className="text-gray-600">
              Are you sure you want to choose {selectedRoosterData.name}?
            </p>
          </div>

          <div className="bg-gray-50 rounded-lg p-6 mb-6">
            <div className="flex items-center mb-4">
              <div className={`w-16 h-16 ${selectedRoosterData.color} rounded-full flex items-center justify-center mr-4`}>
                <span className="text-2xl text-white font-bold">
                  {selectedRoosterData.name.charAt(0)}
                </span>
              </div>
              <div>
                <h3 className="text-xl font-semibold text-gray-800">{selectedRoosterData.name}</h3>
                <p className="text-sm text-gray-600">{selectedRoosterData.type} Type</p>
              </div>
            </div>
            
            <p className="text-gray-700 mb-4">{selectedRoosterData.description}</p>
            
            <div className="grid grid-cols-2 gap-2 text-sm">
              <div className="flex justify-between">
                <span>Strength:</span>
                <span className="font-semibold">{selectedRoosterData.stats.strength}</span>
              </div>
              <div className="flex justify-between">
                <span>Speed:</span>
                <span className="font-semibold">{selectedRoosterData.stats.speed}</span>
              </div>
              <div className="flex justify-between">
                <span>Stamina:</span>
                <span className="font-semibold">{selectedRoosterData.stats.stamina}</span>
              </div>
              <div className="flex justify-between">
                <span>Defense:</span>
                <span className="font-semibold">{selectedRoosterData.stats.defense}</span>
              </div>
              <div className="flex justify-between col-span-2">
                <span>Intelligence:</span>
                <span className="font-semibold">{selectedRoosterData.stats.intelligence}</span>
              </div>
            </div>
          </div>

          <div className="flex gap-3">
            <button
              onClick={() => setShowConfirmation(false)}
              className="flex-1 bg-gray-500 hover:bg-gray-600 text-white font-semibold py-2 px-4 rounded-lg"
            >
              Go Back
            </button>
            <button
              onClick={confirmSelection}
              className="flex-1 bg-blue-600 hover:bg-blue-700 text-white font-semibold py-2 px-4 rounded-lg"
            >
              Confirm
            </button>
          </div>
        </div>
      </div>
    )
  }

  return (
    <div className="min-h-screen bg-gradient-to-br from-slate-900 to-slate-800 p-4">
      <div className="max-w-4xl mx-auto">
        <div className="text-center mb-8">
          <h2 className="text-4xl font-bold text-white mb-4">Choose Your Starter Rooster</h2>
          <p className="text-gray-300 text-lg">
            Select one of these four legendary roosters to begin your journey
          </p>
        </div>

        <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
          {starterRoosters.map((rooster) => (
            <div
              key={rooster.id}
              className="bg-white rounded-lg shadow-xl p-6 hover:shadow-2xl transition-shadow cursor-pointer transform hover:scale-105 transition-transform"
              onClick={() => handleSelection(rooster.id)}
            >
              <div className="flex items-center mb-4">
                <div className={`w-20 h-20 ${rooster.color} rounded-full flex items-center justify-center mr-4`}>
                  <span className="text-3xl text-white font-bold">
                    {rooster.name.charAt(0)}
                  </span>
                </div>
                <div>
                  <h3 className="text-2xl font-bold text-gray-800">{rooster.name}</h3>
                  <p className="text-lg text-gray-600">{rooster.type} Type</p>
                </div>
              </div>
              
              <p className="text-gray-700 mb-4">{rooster.description}</p>
              
              <div className="space-y-2">
                <h4 className="font-semibold text-gray-800 mb-2">Base Stats:</h4>
                <div className="grid grid-cols-2 gap-2 text-sm">
                  <div className="flex justify-between">
                    <span>Strength:</span>
                    <span className="font-semibold">{rooster.stats.strength}</span>
                  </div>
                  <div className="flex justify-between">
                    <span>Speed:</span>
                    <span className="font-semibold">{rooster.stats.speed}</span>
                  </div>
                  <div className="flex justify-between">
                    <span>Stamina:</span>
                    <span className="font-semibold">{rooster.stats.stamina}</span>
                  </div>
                  <div className="flex justify-between">
                    <span>Defense:</span>
                    <span className="font-semibold">{rooster.stats.defense}</span>
                  </div>
                  <div className="flex justify-between col-span-2">
                    <span>Intelligence:</span>
                    <span className="font-semibold">{rooster.stats.intelligence}</span>
                  </div>
                </div>
              </div>
              
              <button className="w-full mt-4 bg-blue-600 hover:bg-blue-700 text-white font-semibold py-2 px-4 rounded-lg transition-colors">
                Choose {rooster.name}
              </button>
            </div>
          ))}
        </div>
      </div>
    </div>
  )
}
