'use client'

import { useState } from 'react'
import { useAccount } from 'wagmi'
import WagerMatchModal from './WagerMatchModal'

interface BattleFinderProps {
  onBack: () => void
}

interface BattleRoom {
  id: string
  player: string
  rooster: string
  wager?: {
    token: string
    amount: string
  }
  isWager: boolean
}

const mockBattleRooms: BattleRoom[] = [
  {
    id: '1',
    player: 'DragonMaster',
    rooster: 'Embawk',
    isWager: false
  },
  {
    id: '2',
    player: 'ThunderBird',
    rooster: 'Boltick',
    wager: { token: 'USDC', amount: '10' },
    isWager: true
  },
  {
    id: '3',
    player: 'AquaChamp',
    rooster: 'Dripple',
    isWager: false
  },
  {
    id: '4',
    player: 'NatureKing',
    rooster: 'Sproutchick',
    wager: { token: 'ETH', amount: '0.01' },
    isWager: true
  }
]

export default function BattleFinder({ onBack }: BattleFinderProps) {
  const [activeTab, setActiveTab] = useState<'casual' | 'wager'>('casual')
  const [showWagerModal, setShowWagerModal] = useState(false)
  const [isSearching, setIsSearching] = useState(false)
  const { isConnected } = useAccount()

  const casualRooms = mockBattleRooms.filter(room => !room.isWager)
  const wagerRooms = mockBattleRooms.filter(room => room.isWager)

  const handleQuickMatch = () => {
    setIsSearching(true)
    // Simulate matchmaking
    setTimeout(() => {
      setIsSearching(false)
      alert('Match found! Starting battle...')
    }, 3000)
  }

  const handleJoinRoom = (roomId: string) => {
    alert(`Joining room ${roomId}...`)
  }

  const handleCreateWager = () => {
    if (!isConnected) {
      alert('Please connect your wallet to create wager matches')
      return
    }
    setShowWagerModal(true)
  }

  return (
    <div className="min-h-screen bg-gradient-to-br from-slate-900 to-slate-800 p-4">
      <div className="max-w-4xl mx-auto">
        <div className="bg-white rounded-lg shadow-xl p-6 mb-6">
          <div className="flex items-center justify-between mb-6">
            <h1 className="text-3xl font-bold text-gray-800">Battle Arena</h1>
            <button
              onClick={onBack}
              className="bg-gray-500 hover:bg-gray-600 text-white px-4 py-2 rounded-lg"
            >
              Back to Menu
            </button>
          </div>

          {/* Tab Navigation */}
          <div className="flex mb-6 border-b">
            <button
              onClick={() => setActiveTab('casual')}
              className={`px-6 py-3 font-semibold ${
                activeTab === 'casual'
                  ? 'text-blue-600 border-b-2 border-blue-600'
                  : 'text-gray-600 hover:text-gray-800'
              }`}
            >
              Casual Battles
            </button>
            <button
              onClick={() => setActiveTab('wager')}
              className={`px-6 py-3 font-semibold ${
                activeTab === 'wager'
                  ? 'text-blue-600 border-b-2 border-blue-600'
                  : 'text-gray-600 hover:text-gray-800'
              }`}
            >
              Wager Matches
            </button>
          </div>

          {/* Quick Match Section */}
          <div className="bg-gray-50 rounded-lg p-6 mb-6">
            <h2 className="text-xl font-semibold text-gray-800 mb-4">Quick Match</h2>
            <p className="text-gray-600 mb-4">
              Find a random opponent for a {activeTab} battle
            </p>
            <button
              onClick={handleQuickMatch}
              disabled={isSearching}
              className="bg-green-600 hover:bg-green-700 disabled:bg-green-400 text-white font-semibold py-3 px-6 rounded-lg"
            >
              {isSearching ? 'Searching for opponent...' : 'Find Quick Match'}
            </button>
          </div>

          {/* Battle Rooms */}
          <div>
            <div className="flex justify-between items-center mb-4">
              <h2 className="text-xl font-semibold text-gray-800">
                {activeTab === 'casual' ? 'Casual Battle Rooms' : 'Wager Match Rooms'}
              </h2>
              {activeTab === 'wager' && (
                <button
                  onClick={handleCreateWager}
                  className="bg-blue-600 hover:bg-blue-700 text-white px-4 py-2 rounded-lg"
                >
                  Create Wager Match
                </button>
              )}
            </div>

            <div className="space-y-3">
              {(activeTab === 'casual' ? casualRooms : wagerRooms).map((room) => (
                <div
                  key={room.id}
                  className="bg-gray-50 rounded-lg p-4 flex items-center justify-between hover:bg-gray-100 transition-colors"
                >
                  <div className="flex items-center space-x-4">
                    <div className="w-12 h-12 bg-blue-500 rounded-full flex items-center justify-center">
                      <span className="text-white font-bold">
                        {room.player.charAt(0)}
                      </span>
                    </div>
                    <div>
                      <h3 className="font-semibold text-gray-800">{room.player}</h3>
                      <p className="text-sm text-gray-600">Rooster: {room.rooster}</p>
                      {room.wager && (
                        <p className="text-sm text-green-600 font-semibold">
                          Wager: {room.wager.amount} {room.wager.token}
                        </p>
                      )}
                    </div>
                  </div>
                  <button
                    onClick={() => handleJoinRoom(room.id)}
                    className="bg-red-600 hover:bg-red-700 text-white px-4 py-2 rounded-lg"
                  >
                    Challenge
                  </button>
                </div>
              ))}
            </div>

            {(activeTab === 'casual' ? casualRooms : wagerRooms).length === 0 && (
              <div className="text-center py-8">
                <p className="text-gray-500">
                  No {activeTab} battles available right now. Try quick match or check back later!
                </p>
              </div>
            )}
          </div>
        </div>
      </div>

      {showWagerModal && (
        <WagerMatchModal
          onClose={() => setShowWagerModal(false)}
          onConfirm={(wagerData) => {
            console.log('Creating wager match:', wagerData)
            setShowWagerModal(false)
            alert('Wager match created! Waiting for opponent...')
          }}
        />
      )}
    </div>
  )
}
