'use client'

import { useState } from 'react'

interface TrainingProps {
  onBack: () => void
}

interface RoosterStats {
  strength: number
  speed: number
  stamina: number
  defense: number
  intelligence: number
  level: number
}

interface PlayerData {
  energy: number
  rooster: {
    name: string
    type: string
    stats: RoosterStats
  }
}

export default function Training({ onBack }: TrainingProps) {
  const [playerData, setPlayerData] = useState<PlayerData>({
    energy: 85,
    rooster: {
      name: 'Embawk',
      type: 'Fire',
      stats: {
        strength: 85,
        speed: 70,
        stamina: 75,
        defense: 65,
        intelligence: 70,
        level: 1
      }
    }
  })

  const [isTraining, setIsTraining] = useState(false)
  const [trainingResult, setTrainingResult] = useState<string | null>(null)

  const energyCost = 10
  const canTrain = playerData.energy >= energyCost

  const trainStat = async (statName: keyof Omit<RoosterStats, 'level'>) => {
    if (!canTrain) return

    setIsTraining(true)
    setTrainingResult(null)

    try {
      // Simulate training time
      await new Promise(resolve => setTimeout(resolve, 2000))

      // Calculate stat increase (1-3 points)
      const increase = Math.floor(Math.random() * 3) + 1
      const newStats = {
        ...playerData.rooster.stats,
        [statName]: playerData.rooster.stats[statName] + increase
      }

      // Check if level should increase (every 50 total stat points)
      const oldTotal = Object.values(playerData.rooster.stats).reduce((sum, val) => sum + val, 0) - playerData.rooster.stats.level
      const newTotal = Object.values(newStats).reduce((sum, val) => sum + val, 0) - newStats.level
      
      if (Math.floor(newTotal / 50) > Math.floor(oldTotal / 50)) {
        newStats.level += 1
        setTrainingResult(`${statName.charAt(0).toUpperCase() + statName.slice(1)} increased by ${increase}! Level up! Now level ${newStats.level}!`)
      } else {
        setTrainingResult(`${statName.charAt(0).toUpperCase() + statName.slice(1)} increased by ${increase}!`)
      }

      setPlayerData(prev => ({
        ...prev,
        energy: prev.energy - energyCost,
        rooster: {
          ...prev.rooster,
          stats: newStats
        }
      }))

    } catch (error) {
      console.error('Training failed:', error)
      setTrainingResult('Training failed. Please try again.')
    } finally {
      setIsTraining(false)
    }
  }

  const getStatColor = (statName: string) => {
    switch (statName) {
      case 'strength': return 'bg-red-500'
      case 'speed': return 'bg-yellow-500'
      case 'stamina': return 'bg-green-500'
      case 'defense': return 'bg-blue-500'
      case 'intelligence': return 'bg-purple-500'
      default: return 'bg-gray-500'
    }
  }

  const getStatDescription = (statName: string) => {
    switch (statName) {
      case 'strength': return 'Increases physical attack damage'
      case 'speed': return 'Determines turn order and evasion'
      case 'stamina': return 'Increases maximum HP'
      case 'defense': return 'Reduces incoming damage'
      case 'intelligence': return 'Improves status effect accuracy'
      default: return ''
    }
  }

  return (
    <div className="min-h-screen bg-gradient-to-br from-slate-900 to-slate-800 p-4">
      <div className="max-w-4xl mx-auto">
        <div className="bg-white rounded-lg shadow-xl p-6 mb-6">
          <div className="flex items-center justify-between mb-6">
            <h1 className="text-3xl font-bold text-gray-800">Training Ground</h1>
            <button
              onClick={onBack}
              className="bg-gray-500 hover:bg-gray-600 text-white px-4 py-2 rounded-lg"
            >
              Back to Menu
            </button>
          </div>

          {/* Energy Display */}
          <div className="bg-blue-50 rounded-lg p-4 mb-6">
            <div className="flex items-center justify-between">
              <div>
                <h2 className="text-lg font-semibold text-blue-800">Energy</h2>
                <p className="text-sm text-blue-600">Required for training: {energyCost} energy per session</p>
              </div>
              <div className="text-right">
                <div className="text-2xl font-bold text-blue-800">{playerData.energy}</div>
                <div className="text-sm text-blue-600">Available</div>
              </div>
            </div>
            <div className="mt-2 bg-blue-200 rounded-full h-2">
              <div 
                className="bg-blue-600 h-2 rounded-full transition-all duration-300"
                style={{ width: `${Math.min(playerData.energy, 100)}%` }}
              />
            </div>
          </div>

          {/* Rooster Info */}
          <div className="bg-gray-50 rounded-lg p-6 mb-6">
            <div className="flex items-center mb-4">
              <div className="w-16 h-16 bg-red-500 rounded-full flex items-center justify-center mr-4">
                <span className="text-2xl text-white font-bold">
                  {playerData.rooster.name.charAt(0)}
                </span>
              </div>
              <div>
                <h2 className="text-2xl font-bold text-gray-800">{playerData.rooster.name}</h2>
                <p className="text-gray-600">{playerData.rooster.type} Type ‚Ä¢ Level {playerData.rooster.stats.level}</p>
              </div>
            </div>
          </div>

          {/* Training Result */}
          {trainingResult && (
            <div className="bg-green-50 border border-green-200 rounded-lg p-4 mb-6">
              <p className="text-green-800 font-semibold">{trainingResult}</p>
            </div>
          )}

          {/* Training Options */}
          <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-4">
            {Object.entries(playerData.rooster.stats).map(([statName, statValue]) => {
              if (statName === 'level') return null
              
              return (
                <div key={statName} className="bg-white border border-gray-200 rounded-lg p-4">
                  <div className="flex items-center mb-3">
                    <div className={`w-8 h-8 ${getStatColor(statName)} rounded-full flex items-center justify-center mr-3`}>
                      <span className="text-white text-sm font-bold">
                        {statName.charAt(0).toUpperCase()}
                      </span>
                    </div>
                    <div>
                      <h3 className="font-semibold text-gray-800 capitalize">{statName}</h3>
                      <p className="text-2xl font-bold text-gray-900">{statValue}</p>
                    </div>
                  </div>
                  
                  <p className="text-xs text-gray-600 mb-4">
                    {getStatDescription(statName)}
                  </p>
                  
                  <button
                    onClick={() => trainStat(statName as keyof Omit<RoosterStats, 'level'>)}
                    disabled={!canTrain || isTraining}
                    className={`w-full py-2 px-4 rounded-lg font-semibold transition-colors ${
                      canTrain && !isTraining
                        ? `${getStatColor(statName)} hover:opacity-80 text-white`
                        : 'bg-gray-300 text-gray-500 cursor-not-allowed'
                    }`}
                  >
                    {isTraining ? 'Training...' : `Train ${statName.charAt(0).toUpperCase() + statName.slice(1)}`}
                  </button>
                </div>
              )
            })}
          </div>

          {!canTrain && (
            <div className="mt-6 bg-yellow-50 border border-yellow-200 rounded-lg p-4">
              <p className="text-yellow-800">
                <span className="font-semibold">Not enough energy!</span> You need at least {energyCost} energy to train. 
                Energy regenerates over time or can be purchased.
              </p>
            </div>
          )}

          {/* Training Tips */}
          <div className="mt-6 bg-gray-50 rounded-lg p-4">
            <h3 className="font-semibold text-gray-800 mb-2">Training Tips</h3>
            <ul className="text-sm text-gray-600 space-y-1">
              <li>‚Ä¢ Each training session costs {energyCost} energy and increases a stat by 1-3 points</li>
              <li>‚Ä¢ Your rooster levels up every 50 total stat points gained</li>
              <li>‚Ä¢ Higher level roosters perform better in battles</li>
              <li>‚Ä¢ Energy regenerates slowly over time</li>
              <li>‚Ä¢ Focus on training stats that complement your rooster's type</li>
            </ul>
          </div>
        </div>
      </div>
    </div>
  )
}
